import React, { Component } from 'react'
import { NavBar, Icon, Tabs, WhiteSpace, Flex, WingBlank } from 'antd-mobile';
import Rec from './Rec'

const tabs = [
    { title: '推荐' },
    { title: '冬季' },
    { title: '宜家' },
    { title: '小清新' },
    { title: '小户型' },
    { title: '个性色彩' },
    { title: '其他' },
];

export default class Ins extends Component {
    state = {
        imgHeight: '200px',
    }

    render() {
        return (
            <div>
                <NavBar
                    style={{ backgroundColor: '#3fcccb', color: '#ffffff', fontSize: '36px', fontFamily: 'Adobe 黑体 Std R', fontWeight: '500', letterSpacing: '2px' }}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    ]}
                >灵感</NavBar>
                <div>
                    <Tabs tabs={tabs} initialPage={0} renderTabBar={props => <Tabs.DefaultTabBar {...props} page={6} />} tabBarActiveTextColor='#3fcccb' tabBarUnderlineStyle={{ display: 'none' }}>
                        <div className="flex-container" style={{backgroundColor: '#eeeeee' }}>
                            {[1, 2, 3, 4, 5].map((val) => (
                                <div>
                                    <WhiteSpace size="lg" />
                                    <WingBlank>
                                        <Flex>
                                            <Flex.Item><Rec /></Flex.Item>
                                            <Flex.Item><Rec /></Flex.Item>
                                        </Flex>
                                    </WingBlank>
                                </div>
                            ))}
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#eeeeee' }}>
                            冬季
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#eeeeee' }}>
                            宜家
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#eeeeee' }}>
                            小清新
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#eeeeee' }}>
                            小户型
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#eeeeee' }}>
                            个性色彩
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#eeeeee' }}>
                            其他
                        </div>
                    </Tabs>
                </div>
            </div>
        )
    }
}
