import React, { Component } from 'react'
import { NavBar, SearchBar, WhiteSpace, WingBlank, Carousel, Grid, Flex } from 'antd-mobile';

const list = ['桌','床','椅','几','柜','书架','沙发','家居饰品','户外家具','其他'];
const data = Array.from(list).map((_val, i) => ({
    icon: 'images/desk.png',
    text: `${_val}`,
}));

export default class Store extends Component {

    state = {
        imgHeight: '200px',
    }

    render() {
        return (
            <div>
                <NavBar
                    style={{ backgroundColor: '#3fcccb', color: '#ffffff', fontSize: '36px', fontFamily: 'Adobe 黑体 Std R', fontWeight: '500', letterSpacing: '2px'}}
                    rightContent={[
                        <img src='images/buy.svg' style={{ width: '20px', height: '20px', marginRight: '16px' }} />,
                    ]}
                >商城</NavBar>
                <div style={{position:'relative',height:'200px'}}>
                <SearchBar
                    placeholder="输入关键字搜索"
                    style={{ zIndex:'100',opacity:'0.9'}}
                />
                <Carousel
                    autoplay={true}
                    infinite
                    style={{position:'absolute',top:'0'}}
                >
                    <a
                        href="#"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img src='images/store_banner.png' style={{ width: '100%', height: '200px' }} />
                    </a>
                    <a
                        href="#"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img src='images/store_banner.png' style={{ width: '100%', height: '200px' }} />
                    </a>
                    <a
                        href="#"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img src='images/store_banner.png' style={{ width: '100%', height: '200px' }} />
                    </a>
                    <a
                        href="#"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img src='images/store_banner.png' style={{ width: '100%', height: '200px' }} />
                    </a>
                </Carousel>
                </div>
                <div>
                    <Grid 
                        data={data} 
                        activeStyle={false} 
                        columnNum={5}
                        itemStyle={{height:'81px'}}
                    />
                </div>
                <div className="flex-container">
                    <WingBlank>
                        <WhiteSpace />
                        <Flex>
                            <Flex.Item style={{width:'128px',fontSize:'12px'}}>
                                <img src='images/cup.png' style={{width:'100%',height:'118px'}}/>
                                <p>Top Art Studio 欧式风格精细...</p>
                                <p>¥ 39.95</p>
                            </Flex.Item>
                            <Flex.Item style={{width:'128px',fontSize:'12px'}}>
                                <img src='images/clock.png' style={{width:'100%',height:'118px'}}/>
                                <p>顺顺工艺欧式风格塑料外框黑...</p>
                                <p>¥ 83.99</p>
                            </Flex.Item>
                        </Flex>
                        <img src='images/bed.png' style={{width:'100%',height:'118px'}}/>
                    </WingBlank>
                </div>
            </div>
        )
    }
}
