import React, { Component } from 'react'

export default class Rec extends Component {
    render() {
        return (
            <div style={{height:'200px',width:'165px',backgroundColor:'#fff',borderRadius:'5px',margin:'0 0',overflow:'hidden'}}>
                <img src='images/pic.png' style={{height:'167.5px',width:'100%'}}/>
                <img src='images/head.png' style={{height:'30px',width:'30px',position:'relative',top:'-10px',left:'5px',float:'left'}}/>
                <div style={{float:'left',lineHeight:'25px',marginLeft:'10px',fontSize:'12px'}}>橙色律动</div>
                <img src='images/like.png' style={{height:'13px',width:'13px',float:'right',margin:'5px 5px'}}/>
            </div>
        )
    }
}
