import React, { Component } from 'react'
import { TabBar } from 'antd-mobile';
import Home from '../components/Home'
import Ins from '../components/Ins'
import Store from '../components/Store';

export default class AppTab extends Component {

    constructor(props) {
        super(props);
        this.state = {
          selectedTab: 'blueTab',
        };
    }

    
    render() {
        return (
            <div style={{position: 'fixed', height: '100%', width: '100%', top: 0 }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#3fcccb"
                    barTintColor="white"
                    hidden={this.state.hidden}
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={<div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(icon_1/shouye.svg) center center /  21px 21px no-repeat' }}
                            />
                        }
                        selectedIcon={<div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(icon_2/shouye.svg) center center /  21px 21px no-repeat' }}
                            />
                        }
                        selected={this.state.selectedTab === 'blueTab'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'blueTab',
                            });
                        }}
                        data-seed="logId"
                    >
                        <Home />
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(icon_1/linggan-copy.svg) center center /  21px 21px no-repeat' }}
                            />
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(icon_2/linggan-copy.svg) center center /  21px 21px no-repeat' }}
                            />
                        }
                        title="灵感"
                        key="Koubei"
                        selected={this.state.selectedTab === 'redTab'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'redTab',
                            });
                        }}
                        data-seed="logId1"
                    >
                        <Ins />
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(icon_1/store_icon.svg) center center /  21px 21px no-repeat' }}
                            />
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(icon_2/store_icon.svg) center center /  21px 21px no-repeat' }}
                            />
                        }
                        title="商城"
                        key="Friend"
                        // dot
                        selected={this.state.selectedTab === 'greenTab'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'greenTab',
                            });
                        }}
                    >
                        <Store />
                    </TabBar.Item>
                    <TabBar.Item
                        icon={{ uri: 'icon_1/wode.svg' }}
                        selectedIcon={{ uri: 'icon_2/wode.svg' }}
                        title="我的"
                        key="my"
                        selected={this.state.selectedTab === 'yellowTab'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'yellowTab',
                            });
                        }}
                    >
                        我的
                    </TabBar.Item>
                </TabBar>
            </div>
        )
    }
}
