import React, { Component } from 'react'
import { NavBar, Icon, Flex, WhiteSpace, WingBlank, Carousel } from 'antd-mobile';

export default class Home extends Component {

    state = {
        imgHeight: '200px',
    }

    render() {
        return (
            <div>
                <NavBar
                    style={{ backgroundColor: '#3fcccb', color: '#ffffff', fontSize:'36px', fontFamily:'Adobe 黑体 Std R', fontWeight:'500',letterSpacing:'2px' }}
                    // rightContent={[
                    //     <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    // ]}
                >住吧家居</NavBar>
                <Carousel
                    autoplay={true}
                    infinite
                    // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    // afterChange={index => console.log('slide to', index)}
                >
                    
                    <a
                        href="#"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img src='images/banner-1.png' style={{width: '100%', height: '200px' }}/>
                    </a>
                    <a
                        href="#"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img src='images/banner-2.png' style={{width: '100%', height: '200px' }}/>
                    </a>
                </Carousel>
                <div className="flex-container" style={{backgroundColor:'#eeeeee'}}> 
                    <WhiteSpace size="lg" />
                    <WingBlank>
                    <Flex>
                            <Flex.Item>
                                <a href='#' style={{height:'110px'}}>
                                    <img src='images/hot.png' style={{height:'100%',width:'100%'}}/>
                                </a>
                            </Flex.Item>
                            <Flex.Item>
                                <a href='#' style={{height:'110px'}}>
                                    <img src='images/siren.png' style={{height:'100%',width:'100%'}}/>
                                </a>
                            </Flex.Item>
                            <Flex.Item>
                                <a href='#' style={{height:'110px'}}>
                                    <img src='images/tips.png' style={{height:'100%',width:'100%'}}/>
                                </a>
                            </Flex.Item>
                    </Flex>
                    </WingBlank>
                    <WhiteSpace size="lg" />
                </div>
                <div>
                    <p style={{borderLeft:'5px solid #3fcccb', fontSize:'20px',marginTop:'10px',marginBottom:'10px'}}>热门推荐</p>
                    <a href='#' style={{height:'180px'}}>
                        <img src='images/hot_big.jpg' style={{height:'100%',width:'100%'}}/>
                    </a>
                </div>
            </div>
        )
    }
}
